.loader_wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader_img {
  width: 100px;
}

.cls-1,
.cls-2,
.cls-5 {
  fill: none;
}
.cls-1,
.cls-2 {
  stroke: #727272;
  stroke-linecap: round;
  stroke-miterlimit: 10;
}
.cls-1 {
  stroke-width: 3px;
}
.cls-2,
.cls-5 {
  stroke-width: 4px;
}
.cls-3 {
  fill: #fff;
}
.cls-4 {
  fill: #424242;
  opacity: 0.5;
}
.progress {
  stroke: #6262e6;
  stroke-linejoin: round;
  stroke-linecap: round;
}
.progress2 {
  fill: none;
  stroke-width: 6px;
}
.hamsterFill {
  fill: #999;
}

.progressGroup {
  animation: 2s linear 1s infinite ProgressOpacity;
  opacity: 0.25;
}

.progress {
  animation: 1.5s ease-in-out infinite alternate Pulse;
}

.progress1 {
  animation: 2s linear 1s infinite Progress1;
  stroke-dasharray: 0 1000;
}

.progress2 {
  animation: 2s linear 1s infinite Progress2;
  stroke-dasharray: 0 0 1 1000;
  display: none;
}

@keyframes ProgressOpacity {
  to {
    opacity: 1;
  }
}
@keyframes Progress1 {
  to {
    stroke-dasharray: 280 1000;
  }
}
@keyframes Progress2 {
  to {
    stroke-dasharray: 0 280 1 1000;
  }
}
@keyframes Pulse {
  to {
    stroke: #ff0000;
  }
}
#wheel {
  animation: 1.5s linear infinite Spin;
  transform: rotate(0deg);
  transform-origin: 50px 50px;
}

@keyframes Spin {
  to {
    transform: rotate(360deg);
  }
}
#whisky {
  animation: 1.5s ease-in-out infinite alternate Rock;
  transform: rotate(20deg) translate(18px, 49px);
  transform-origin: 50px 50px;
}

@keyframes Rock {
  to {
    transform: rotate(-20deg) translate(18px, 49px);
  }
}
.legs {
  transform-origin: 40px -40px;
  transform: translatex(1px) rotate(2deg);
  animation: 0.25s ease-in-out infinite alternate Wiggle;
}

@keyframes Wiggle {
  50% {
    transform: translatex(-1px) rotate(-2deg);
  }
}
